import React, { Component } from "react";
import SignupForm from "../components/Signup/SignupForm";
import API from "../utils/API";



//saved entries - register and save to database 
class Signup extends React.Component {

        state = {
            value: "",
            users: []
        };
      
      componentDidMount() {
        this.userSignup();
      }
      
      //creates user data
      newUser = userData => {
        return {
            email: userData.email,
            password: userData.password
        }
      }
      
      
      userSignup = query => {
        API.saveUser(query)
            .then(res => this.setState({ users: res.data.items.map(userData => this.newUser(userData)) }))
            .catch(err => console.error(err));
      };
      
      
      handleInputChange = event => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name]: value
        });
      };
      
      //actions for submit button
        handleFormSubmit = event => {
            event.preventDefault();
            this.userSignup(this.state.email);
            this.userSignup(this.state.password);
        };



    render(){
        return (
        
            <div>
                <SignupForm 
                email={this.state.email}
                password={this.state.password}
                handleInputChange={this.handleInputChange}
                handleFormSubmit={this.handleFormSubmit}XF
                />

            </div>
            
            )
        
        }
}

export default Signup;